import { Component, OnInit, ViewChild } from '@angular/core';
import { NzModalRef, NzMessageService, NzDrawerService } from 'ng-zorro-antd';
import { _HttpClient } from '@delon/theme';
import { STColumn, STReq, STComponent } from '@delon/abc';
import { SysAdminUserEditComponent } from '../edit/edit.component';

@Component({
  selector: 'app-sys-admin-user-view',
  templateUrl: './view.component.html',
})
export class SysAdminUserViewComponent implements OnInit {

  url = `/SysUser`;

  columns: STColumn[] = [
    { title: '', type: 'no' },
    { title: '', index: 'account' },
    { title: '', index: 'headIcon', type: 'img', width: '50px'},
    { title: '', index: 'realName'},
    { title: '', index: 'email'},
    { title: '', index: 'organizeName'},
    { title: '', index: 'roleName'},
    { title: '', index: 'logOnCount'},
    { title: '', index: 'isAdministrator', type :'yn'},
    { title: '', index: 'multiUserLogin',type:'yn'},
    { title: '', index: 'creatorUserName'},
    { title: '', index: 'creatorTime',type:'date'},
    {
      title: '',
      buttons: [
        {
          text: 'Edit',
          icon: 'edit',
          click: (record: any) => {
              this.openComponent(record.id);
          }
        },
        {
          icon: 'delete',
          type: 'del',
          click: (record: any, modal: any) => {

          }
        },
      ],
    },
  ];

  @ViewChild('st') st:STComponent;

  req:STReq = {
    //method:'POST',
    //headers:'content-type: text/plain'
  }
  
  constructor(
    private drawerService: NzDrawerService,
    public msgSrv: NzMessageService,
    public http: _HttpClient
  ) { }

  ngOnInit(): void {
  }

  openComponent(id): void {
    const drawerRef = this.drawerService.create<SysAdminUserEditComponent, { Id: string }, string>({
      nzTitle: '',
      nzContent: SysAdminUserEditComponent,
      nzMaskClosable:true,
      nzWidth:650,
      nzContentParams: {
        Id: id
      }
    });

    drawerRef.afterOpen.subscribe(() => {
      console.log('Drawer(Component) open');
    });

    drawerRef.afterClose.subscribe(data => {
      if (data === 'ok') {
         this.st.reload();
      }
    });
  }


}
